<template>
    <div  class="medal-container">

        <div v-if="isReadData && curMedalNum < listMedals.length-1" @click="nextMedal" class="right-button"></div>
        <div v-if="curMedalNum > 0" @click="prevMedal" class="left-button"></div>

        <div class="info-block-medal main-content">
            <div @click="this.$parent.closeListMedals" class="close-list-btn"></div>
            <div class="gray-block"></div>

            <div ref="blockWithInfo" class="inner-block-medal">
                <div class="info-block-title info-block-title-black">
                    {{listMedals[curMedalNum].desc}}
                </div>
                <ul>
                    <li v-bind:key="index" v-for="(best,index) in listMedals[curMedalNum].list_best" ><span v-html="best"></span></li>
                </ul>
                <ContactForm :title-text="'Заполните форму ниже если хотите подобный значок!'"/>
                <img class="bloc-medal-img" v-bind:src="'/img/MedalList/medal_'+(listMedals[curMedalNum].medal_num )+'.png'" alt="">

            </div>


        </div>
    </div>
</template>

<script>
    import axios from "axios";

    import ContactForm from "@/components/ContactForm";
    import Velocity from 'velocity-animate'

    export default {
        name: "ListMedals",
        data() {
            return {
                listMedals: [],
                curMedalNum: 0,
                isRunAnim: false,
                isReadData:false
            }
        },
        methods: {
            nextMedal() {

                if(this.isRunAnim) return

                this.isRunAnim = true;
                var main = this;
                var blockAnim = this.$refs.blockWithInfo;
                Velocity(blockAnim, {opacity: '0'}, {duration: 400, complete: function () {
                        main._next()

                        Velocity(blockAnim, {opacity: '1'}, {duration: 400, })
                    }})


            },
            _next() {
                this.curMedalNum ++;
                if(this.curMedalNum >= this.listMedals.length) {
                    this.curMedalNum = this.listMedals.length-1;
                }
                this.isRunAnim = false
            },
            _prev() {
                this.curMedalNum --;
                if(this.curMedalNum < 0) {
                    this.curMedalNum = 0;
                }
                this.isRunAnim = false

            },
            prevMedal() {

                if(this.isRunAnim) return

                this.isRunAnim = true;
                var main = this;
                var blockAnim = this.$refs.blockWithInfo;
                Velocity(blockAnim, {opacity: '0'}, {duration: 400, complete: function () {
                        main._prev()

                        Velocity(blockAnim, {opacity: '1'}, {duration: 400, })
                    }})
            }
        },
        mounted() {
            this.curMedalNum = this.$parent.numOpenList
            axios.get( 'https://scnipper.github.io/medal_list.json').then(response => {
                this.listMedals = response.data;
                this.isReadData = true;
            })

        },
        components: {
            ContactForm
        }
    }
</script>

