<template>
    <div class="social">
        <div class="round-social" v-if="!isHover && !isOpen"></div>
        <div @mouseenter="isHover = true" @mouseleave="isHover = false"
             @click="openAllSocialButtons"
             v-click-outside="clickOutside"
             :style="{'background-color': isHover ? '#1463DB' : '#061220'}" class="social-btn">
            <img style="width: 1.8rem" v-if="isHover && !isOpen" src="../assets/social_icon.png" alt="">
            <img style="width: 1.8rem" v-if="!isHover && !isOpen" src="../assets/social_icon_first.png" alt="">
            <img style="width: 0.9rem" v-if="isOpen" src="../assets/arrow_social.png" alt="">


        </div>
        <transition v-bind:key="index" v-for="(ic,index) in icons" @before-enter="beforeEnter" @enter="enterAnim"
                    @leave="leaveAnim" v-bind:css="false">
            <div @click="openLink(ic.link)"
                 @mouseenter="widthChange"
                 @mouseleave="widthChangeLeave"
                 v-if="isOpen" class="small-social-btn"
                 :id='index +1'>
                <div :class="'small-social-btn-sub '+ ic.isChangeWidth">
                    <transition
                            @before-enter="beforeEnter"
                            @enter="enterAnimPhone"
                            @leave="leaveAnimPhone">
                        <div v-if="ic.isChangeWidth !== '' && isShowPhone" class="text-phone">
                            <img src="/img/SocialIcons/whats.png" alt="">
                            <span>+7 (953) 723-86-75</span>
                        </div>
                    </transition>

                </div>
                <div class="small-social-img"
                     :style="{'background-image': 'url(/img/SocialIcons/'+ic.icon+')','background-size':ic.width+'rem'}"
                ></div>
            </div>
        </transition>

    </div>


</template>

<script>
    import Velocity from 'velocity-animate'
    import ClickOutside from 'vue-click-outside'

    export default {
        name: "SocialButton",
        data() {
            return {
                isOpen: false,
                isHover: false,
                isShowPhone:false,
                icons: [

                    {
                        width: 1.65,
                        link: "tel:79537238675",
                        icon: "phone.png",
                        isChangeWidth: "changeWidth"
                    },
                    {
                        width: 1.65,
                        link: "https://mail.ru",
                        icon: "mail.png",
                        isChangeWidth: ""
                    },
                    {
                        width: 1.55,
                        link: "https://vk.com",
                        icon: "vk.png",
                        isChangeWidth: ""
                    },
                    {
                        width: 0.85,
                        link: "https://ok.ru",
                        icon: "ok.png",
                        isChangeWidth: ""
                    },


                    {
                        width: 1.3,
                        link: "https://instagram.com",
                        icon: "inst.png",
                        isChangeWidth: ""
                    }
                ]
            }
        },
        methods: {
            clickOutside() {
                if(!this.isHover) {
                    this.isOpen = false
                }
            },
            openAllSocialButtons() {
                this.isOpen = !this.isOpen;
            },
            beforeEnter(el) {
                el.style.opacity = 0;
            },
            enterAnimPhone(el,done) {
                Velocity(el, {opacity: 1}, {duration: 600, complete: done})

            },
            leaveAnimPhone(el) {
                el.style.opacity = 0;
            },
            enterAnim(el, done) {
                const _y = 2 + (4 * el.id);
                Velocity(el, {bottom: _y + 'rem', opacity: 1}, {duration: 300, complete: done})
            },
            leaveAnim(el, done) {
                Velocity(el, {bottom: 0, opacity: 0}, {duration: 300, complete: done})
            },
            openLink(link) {
                if(link !== "")
                window.open(link, '_blank')
            },
            widthChange(event) {
                if (event.target.style.opacity == 1) {
                    var firstElementChild = event.target.firstElementChild;
                    if (firstElementChild.classList.length > 1) {
                        this.isShowPhone = true;
                        Velocity(firstElementChild, {width: '15rem', right: '0rem'}, {duration: 200})

                    }
                }


            },
            widthChangeLeave(event) {
                var firstElementChild = event.target.firstElementChild;
                if (firstElementChild.classList.length > 1) {
                    this.isShowPhone = false;
                    Velocity(firstElementChild, {width: '3.1rem', right: '0'}, {duration: 200})
                }
            }
        },
        directives: {
            ClickOutside
        }
    }
</script>

