        width: 4.4rem;
        height: 4.4rem;
        position: relative;
        z-index: 999;
        border-radius: 2.2rem;
        cursor: pointer;
        transition: background-color .3s;
        display: flex;
        justify-content: center;
        align-items: center;
    }


    .small-social-btn {
        position: absolute;
