        align-items: center;
        cursor: pointer;

    }

    .small-social-btn-sub {
        position: absolute;
        z-index: 1001;
        width: 3.1rem;
        height: 3.1rem;
        border-radius: 1.55rem;
        background-color: #fff;
        box-shadow: 3px 3px 7px -3px rgba(0,0,0,0.51);
    }

    .small-social-img {
        position: absolute;
        z-index: 1002;
        width: 3.1rem;
