<template>
    <div id="page5" class="page">
        <div class="main-content page5-content">
            <div class="info-block-title info-block-title-black">Ещё больше наших работ!</div>
            <div class="sub-title-page5">Всего успешно выполнено более 120 заказов</div>
            <div ref="medalList" class="medals-list">
                <div v-bind:key="index" v-for="(medal,index) in curList"
                     @click="openList(index)"
                     class="medal-item">
                    <img v-bind:src="'img/MedalList/medal_'+medal.medal_num+'.png'" alt="" class="medal-item-img">
                    <div class="medal-item-text">{{medal.desc}}</div>

                    <div class="more-medal-info">Подробнее о значке</div>
                </div>

            </div>

            <div class="arrows">
                <img v-if="this.curPos <= 0" @click="moveLeft" src="../assets/arrow_none.png" alt="">
                <img v-if="this.curPos > 0" @click="moveLeft" src="../assets/arrow_compl.png" alt="">
                <img v-if="this.curPos + 5 < this.list.length" class="right-arrow" @click="moveRight" src="../assets/arrow_compl.png" alt="">
                <img v-if="this.curPos + 5 >= this.list.length" class="right-arrow" @click="moveRight" src="../assets/arrow_none.png" alt="">
            </div>
        </div>
    </div>
</template>

<script>
    import Velocity from 'velocity-animate'
    import axios from "axios";

    export default {


        data() {
            return {
                posList: 0,
                isMoveList: false,
                list: null,
                curList: [],
                curPos: 0
            }
        },
        name: "Page5",
        mounted() {
            axios.get('https://scnipper.github.io/medal_list.json').then(response => {
                this.list = response.data;
                this.updateState();
            })

        },
        computed: {
            lig: function () {
                return console;
            }
        },
        methods: {

            openList(num) {
              this.$parent.openListMedals(this.curPos +num)
            },
            hoverItem(medal) {
                medal.isShowOverText = true
            },
            hoverEnd(medal) {
                medal.isShowOverText = false
            },
            completeMoveList() {
                this.isMoveList = false;

            },
            _leftMv() {

                this.curPos -= 5;

                if (this.curPos < 0) this.curPos = 0;

                this.updateState()
            },
            updateState() {
                this.curList = []
                for (var i = this.curPos; i < this.curPos + 5; i++) {
                    if (i < this.list.length) {
                        this.curList.push(this.list[i])
                    }
                }
            },
            _rightMv() {

               // if (this.curPos >= this.list.length) this.curPos = this.list.length;
                this.curPos += 5;

                this.updateState()

            },
            moveLeft() {
                if (this.curPos <= 0 || this.isMoveList) return;

                this.isMoveList = true;
                var el = this.$refs.medalList;

                var main = this;
                Velocity(el, {opacity: '0'}, {
                    duration: 400, complete: function () {
                        main._leftMv()
                        Velocity(el, {opacity: '1'}, {duration: 400,complete:main.completeMoveList})
                    }
                })
            },
            moveRight() {
                if (this.curPos + 5 >= this.list.length || this.isMoveList) return;
                this.isMoveList = true
                var el = this.$refs.medalList;

                var main = this;
                Velocity(el, {opacity: '0'}, {
                    duration: 400, complete: function () {
                        main._rightMv()
                        Velocity(el, {opacity: '1'}, {duration: 400,complete:main.completeMoveList})
                    }
                })


            }
        }
    }
</script>

