<template>
    <div class="topMenu">
        <div class="main-content container-menu">
            <div class="title-top-menu">{{title}}</div>

            <div ref="lel" @click="clickToScroll(link.link)" v-bind:key="index" class="item-menu"
                 v-for="(link,index) in linkList" v-bind:class="{ 'item-menu-hover-static': link.isHover }">
                {{link.name}}
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: "TopMenu",
        data() {
            return {
                heightWindow:0,
                positions: [],
                linkList: [
                    {
                        name: "Контакты",
                        link: "contacts",
                        isHover: false
                    },
                    {
                        name: "Вакансии",
                        link: "vacancies",
                        isHover: false

                    },
                    {
                        name: "Цены",
                        link: "prices",
                        isHover: false
                    },
                    {
                        name: "Отзывы",
                        link: "review",
                        isHover: false
                    },
                    {
                        name: "Почему мы",
                        link: "why_we",
                        isHover: false
                    },
                    {
                        name: "Процесс",
                        link: "process",
                        isHover: false
                    },
                    {
                        name: "Наши работы",
                        link: "our_works",
                        isHover: false
                    },
                    {
                        name: "Услуги",
                        link: "service",
                        isHover: false
                    }
                ]
            }
        },

        methods: {
            clickToScroll(link) {
                this.app.$refs[link].scrollTo()
            },
            handleScroll() {
                for (var i = 0;i < this.linkList.length;i++) {
                    this.linkList[i].isHover = false;
                }
                for (var j = 0;j < this.positions.length;j++) {
                    if( this.positions[j] > window.scrollY) {

                        this.linkList[this.linkList.length-1 - j].isHover = true;
                        break;
                    }
                }




            }
        },
        mounted() {
            this.heightWindow = this.$parent.$refs.all_pages.clientHeight / window.outerHeight;

            var _pos = this.positions;
            this.$parent.$refs.all_pages.querySelectorAll('.pos').forEach(function (child) {
                _pos.push(child.offsetTop+child.clientHeight)
            })
            window.addEventListener('scroll', this.handleScroll)
        },
        computed: {
            lig: function () {
                return console
            }
        },
        props: ["title", "app"]
    }
</script>

