    .topMenu {
        display: flex;
        align-items: flex-end;
        width: 100%;
        height: 4.25rem;
        position: fixed;
        background-color: #071627;
        z-index: 999999;
    }

    .title-top-menu {
        color: #fff;
        font-weight: bold;
        font-size: 1rem;
        float: left;
    }

    .item-menu-hover-static {
        color: #B8BDC4 !important;

    }

    .item-menu:hover {
        color: #B8BDC4;
    }

    .item-menu {
        color: #4C5B6A;
        cursor: pointer;
        font-weight: normal;
        margin-left: 2.1rem;
        transition: color .3s;
        float: right;
    }


    .container-menu {
